import React, { useState, useEffect } from 'react';
import { collection, addDoc, getDocs, updateDoc, deleteDoc, doc } from 'firebase/firestore';
import { db } from '../firebase';
import '../css/App.css';

const UserManagement = () => {
  const [users, setUsers] = useState([]);
  const [newUsername, setNewUsername] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [editUserId, setEditUserId] = useState(null);
  const [editUsername, setEditUsername] = useState('');
  const [editPassword, setEditPassword] = useState('');

  useEffect(() => {
    fetchUsers();
  }, []);

  const fetchUsers = async () => {
    const usersCollection = await getDocs(collection(db, 'users'));
    const usersList = usersCollection.docs.map(doc => ({ id: doc.id, ...doc.data() }));
    setUsers(usersList);
  };

  const handleAddUser = async () => {
    if (newUsername && newPassword) {
      await addDoc(collection(db, 'users'), {
        username: newUsername,
        password: newPassword,
      });
      setNewUsername('');
      setNewPassword('');
      fetchUsers();
    }
  };

  const handleEditUser = (user) => {
    setEditUserId(user.id);
    setEditUsername(user.username);
    setEditPassword(user.password);
  };

  const handleUpdateUser = async () => {
    const userDoc = doc(db, 'users', editUserId);
    await updateDoc(userDoc, {
      username: editUsername,
      password: editPassword,
    });
    setEditUserId(null);
    setEditUsername('');
    setEditPassword('');
    fetchUsers();
  };

  const handleDeleteUser = async (id) => {
    const userDoc = doc(db, 'users', id);
    await deleteDoc(userDoc);
    fetchUsers();
  };

  return (
    <div className="container mt-5">
      <h2>Gerenciamento de Usuários</h2>
      
      <div className="mb-4">
        <h4>Adicionar Novo Usuário</h4>
        <div className="form-group">
          <label>Nome de Usuário:</label>
          <input
            type="text"
            className="form-control"
            value={newUsername}
            onChange={(e) => setNewUsername(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label>Senha:</label>
          <input
            type="password"
            className="form-control"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            required
          />
        </div>
        <button onClick={handleAddUser} className="btn btn-success">Adicionar Usuário</button>
      </div>

      <h4>Lista de Usuários</h4>
      <table className="table table-bordered">
        <thead>
          <tr>
            <th>Nome de Usuário</th>
            <th>Senha</th>
            <th>Ações</th>
          </tr>
        </thead>
        <tbody>
          {users.map((user) => (
            <tr key={user.id}>
              <td>{user.username}</td>
              <td>{user.password}</td>
              <td>
                <button onClick={() => handleEditUser(user)} className="btn btn-primary btn-sm mx-1">Editar</button>
                <button onClick={() => handleDeleteUser(user.id)} className="btn btn-danger btn-sm mx-1">Excluir</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {editUserId && (
        <div className="mt-4">
          <h4>Editar Usuário</h4>
          <div className="form-group">
            <label>Nome de Usuário:</label>
            <input
              type="text"
              className="form-control"
              value={editUsername}
              onChange={(e) => setEditUsername(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label>Senha:</label>
            <input
              type="password"
              className="form-control"
              value={editPassword}
              onChange={(e) => setEditPassword(e.target.value)}
              required
            />
          </div>
          <button onClick={handleUpdateUser} className="btn btn-success">Atualizar Usuário</button>
        </div>
      )}
    </div>
  );
};

export default UserManagement;
