import React, { useState, useEffect } from 'react';
import { collection, query, where, getDocs, doc, updateDoc, deleteDoc, addDoc, serverTimestamp } from 'firebase/firestore';
import { db } from '../firebase';
import jsPDF from 'jspdf';
import 'jspdf-autotable';
import '../css/App.css';
import '../css/Botoes.css';
import '../css/Status.css';

const ActivityForm = () => {
  const [day, setDay] = useState(0);
  const [lastStatus, setLastStatus] = useState('');
  const [activities, setActivities] = useState([]);
  const [editingIndex, setEditingIndex] = useState(null);
  const [editingAll, setEditingAll] = useState(false); // Novo estado para edição em massa

  useEffect(() => {
    const fetchActivities = async () => {
      const q = query(collection(db, 'activities'), where('day', '==', day));
      const querySnapshot = await getDocs(q);
      const activitiesList = querySnapshot.docs
        .map(doc => ({ id: doc.id, ...doc.data() }))
        .sort((a, b) => a.timestamp - b.timestamp);

      setActivities(activitiesList);

      updateLastStatus(activitiesList); // Atualiza o status inicial
    };
    fetchActivities();
  }, [day]);

  const updateLastStatus = (activitiesList) => {
    const lastCompletedActivity = activitiesList
      .filter(activity => activity.actualEnd)
      .slice(-1)[0];

    if (lastCompletedActivity) {
      setLastStatus(lastCompletedActivity.status);
    } else {
      setLastStatus('');
    }
  };

  const getStatusClass = () => {
    if (lastStatus.includes('Atrasado')) {
      return 'badge-danger';
    } else if (lastStatus.includes('Antecipado')) {
      return 'badge-info';
    } else if (lastStatus.includes('Dentro do Horário')) {
      return 'badge-success';
    }
    return 'badge-secondary';
  };

  const getStatusIcon = () => {
    if (lastStatus.includes('Atrasado')) {
      return '❌';
    } else if (lastStatus.includes('Antecipado')) {
      return '⏱️';
    } else if (lastStatus.includes('Dentro do Horário')) {
      return '✅';
    }
    return 'ℹ️';
  };

  const generatePDF = () => {
    const doc = new jsPDF();

    doc.setFontSize(18);
    doc.text('Relatório dos Horários', 14, 22);

    doc.autoTable({
      startY: 15,
      head: [['ID', 'Nome do Evento', 'Início Planejado', 'Fim Planejado', 'Duração Planejada', 'Início Realizado', 'Fim Realizado', 'Duração Realizada', 'Status']],
      body: activities.map((activity, index) => [
        String(index + 1).padStart(2, '0'),
        activity.name,
        activity.plannedStart,
        activity.plannedEnd,
        activity.plannedDuration,
        activity.actualStart,
        activity.actualEnd,
        activity.actualDuration,
        activity.status
      ]),
    });

    doc.save('relatorio_atividades.pdf');
  };

  const handleAddNewActivity = () => {
    const newActivity = {
      name: '',
      plannedStart: activities.length > 0 ? activities[activities.length - 1].plannedEnd : '',
      plannedEnd: '',
      plannedDuration: '',
      actualStart: activities.length > 0 ? activities[activities.length - 1].actualEnd : '',
      actualEnd: '',
      actualDuration: '',
      status: '',
      day,
      timestamp: serverTimestamp()
    };

    setActivities([...activities, newActivity]);
    setEditingIndex(activities.length);
  };

  const calculateDuration = (start, end) => {
    if (!start || !end) return '';
    const startTime = new Date(`1970-01-01T${start}:00Z`);
    const endTime = new Date(`1970-01-01T${end}:00Z`);
    const duration = (endTime - startTime) / 3600000;
    return `${Math.floor(duration)}h ${Math.round((duration % 1) * 60)}min`;
  };

  const calculateStatus = (plannedEnd, actualEnd) => {
    if (!actualEnd) return '';
    const plannedEndTime = new Date(`1970-01-01T${plannedEnd}:00Z`);
    const actualEndTime = new Date(`1970-01-01T${actualEnd}:00Z`);

    if (actualEndTime < plannedEndTime) {
      const diff = (plannedEndTime - actualEndTime) / 3600000;
      return `Antecipado - ${Math.floor(diff)}h ${Math.round((diff % 1) * 60)}min`;
    } else if (actualEndTime > plannedEndTime) {
      const diff = (actualEndTime - plannedEndTime) / 3600000;
      return `Atrasado - ${Math.floor(diff)}h ${Math.round((diff % 1) * 60)}min`;
    } else {
      return 'Dentro do Horário';
    }
  };

  const handleSaveActivity = async (index) => {
    const activity = activities[index];

    if (activity.plannedStart && activity.plannedEnd) {
      activity.plannedDuration = calculateDuration(activity.plannedStart, activity.plannedEnd);
    }

    if (activity.actualStart && activity.actualEnd) {
      activity.actualDuration = calculateDuration(activity.actualStart, activity.actualEnd);
      activity.status = calculateStatus(activity.plannedEnd, activity.actualEnd);
    }

    try {
      if (activity.id) {
        const activityRef = doc(db, 'activities', activity.id);
        await updateDoc(activityRef, activity);
      } else {
        const docRef = await addDoc(collection(db, 'activities'), activity);
        activities[index].id = docRef.id;
      }
      setEditingIndex(null);
    } catch (e) {
      console.error('Error saving document: ', e);
    }
    updateLastStatus(activities);
  };

  const handleSaveAllActivities = async () => {
    try {
      for (let i = 0; i < activities.length; i++) {
        const activity = activities[i];
        await handleSaveActivity(i);
      }
      setEditingAll(false); // Sair do modo de edição em massa após salvar todos
    } catch (e) {
      console.error('Error saving all documents: ', e);
    }
  };

  const handleEditAllActivities = () => {
    setEditingAll(true);
  };

  const handleEditActivity = (index) => {
    setEditingIndex(index);
  };

  const handleCancelActivity = (index) => {
    const activity = activities[index];

    const isEmptyNewActivity = !activity.id && !activity.name && !activity.plannedStart && !activity.plannedEnd && !activity.actualStart && !activity.actualEnd;

    if (isEmptyNewActivity) {
      const updatedActivities = activities.filter((_, i) => i !== index);
      setActivities(updatedActivities);
    }

    setEditingIndex(null);
  };

  const handleInputChange = (index, event) => {
    const { name, value } = event.target;
    const updatedActivities = [...activities];

    if (name === 'plannedEnd') {
      if (new Date(`1970-01-01T${value}:00Z`) < new Date(`1970-01-01T${updatedActivities[index].plannedStart}:00Z`)) {
        alert('A "Hora Fim Planejado" não pode ser anterior à "Hora Início Planejado".');
        return;
      }
    }

    updatedActivities[index][name] = value;

    if (name === 'plannedEnd' && index < activities.length - 1) {
      updatedActivities[index + 1].plannedStart = value;
    }

    if (name === 'actualEnd' && index < activities.length - 1) {
      updatedActivities[index + 1].actualStart = value;
    }

    setActivities(updatedActivities);
  };

  const handleBlur = (index, event) => {
    const { name } = event.target;
    const updatedActivities = [...activities];

    if (name === 'plannedEnd') {
      updatedActivities[index].plannedDuration = calculateDuration(updatedActivities[index].plannedStart, updatedActivities[index].plannedEnd);
    }

    if (name === 'actualEnd') {
      updatedActivities[index].actualDuration = calculateDuration(updatedActivities[index].actualStart, updatedActivities[index].actualEnd);
      updatedActivities[index].status = calculateStatus(updatedActivities[index].plannedEnd, updatedActivities[index].actualEnd);
    }

    setActivities(updatedActivities);
  };

  const handleDeleteActivity = async (index) => {
    const activity = activities[index];
    if (activity.id) {
      try {
        const activityRef = doc(db, 'activities', activity.id);
        await deleteDoc(activityRef);
      } catch (e) {
        console.error('Error deleting document: ', e);
      }
    }
    const updatedActivities = activities.filter((_, i) => i !== index);
    setActivities(updatedActivities);

    updateLastStatus(updatedActivities); // Atualiza o status após a exclusão
  };

  return (
    <div className='principal'>
        <div className="container-fluid">
            <div className="day-buttons">
              {[0, 1, 2, 3, 4, 5].map(d => (
                <button
                  key={d}
                  className={`btn btn-outline-primary mx-1 btn-dia-${d} ${day === d ? 'active' : ''}`}
                  onClick={() => setDay(d)}
                >
                  Dia {d}
                </button>
              ))}
            </div>

            {/* DIV DA TABELA */}
            <div className="day-buttons-3">
              <button type="button" className="btn btn-outline-primary mx-1 addNovo" onClick={handleAddNewActivity}> 📝 Adicionar Novo Momento</button>
              <button type="button" className="btn btn-outline-primary mx-1 geraPdf" onClick={generatePDF}> 📄 Gerar PDF</button>
              <button type="button" className="btn btn-outline-primary mx-1 editaTodos" onClick={handleEditAllActivities}> ✏️ Editar Todos</button> {/* Botão para editar todos */}
              {editingAll && <button type="button" className="btn btn-outline-primary mx-1 salvaTodos" onClick={handleSaveAllActivities}> 💾 Salvar Todos</button>} {/* Botão para salvar todos */}
            </div>

            {/* DIV DO STATUS GERAL */} 
            <div className="status_atual">
              <h5 className="card-title"><span className={`badge ${getStatusClass()}`}>{getStatusIcon()} {lastStatus}</span></h5>
              {/* <button type="button" className="btn btn-secondary ml-2" onClick={() => updateLastStatus(activities)}>Atualizar Status</button> Botão para atualizar status */}
            </div>
        </div>

        {/* DIV DA TABELA */}
        <div className='table'>
          <table className="table table-bordered">
            <thead className="thead-light">
              <tr>
                <th scope="col">ID</th>
                <th scope="col">Momentos</th>
                <th scope="col">Início Planejado</th>
                <th scope="col">Fim Planejado</th>
                <th scope="col">Duração Planejada</th>
                <th scope="col">Início Realizado</th>
                <th scope="col">Fim Realizado</th>
                <th scope="col">Duração Realizada</th>
                <th scope="col" style={{ width: '200px' }} className="text-center" >Status</th>
                <th scope="col" style={{ width: '200px' }} className="text-center" >Ações</th>
              </tr>
            </thead>
            <tbody>
              {activities.map((activity, index) => (
                <tr key={index}>
                  <td data-label="ID">{String(index + 1).padStart(2, '0')}</td>
                  <td data-label="Momentos">
                    {editingIndex === index || editingAll ? (
                      <input type="text" name="name" value={activity.name} onChange={(e) => handleInputChange(index, e)} className="form-control" />
                    ) : (
                      activity.name
                    )}
                  </td>
                  <td data-label="Início Planejado">
                    {editingIndex === index || editingAll ? (
                      <input
                        type="time"
                        name="plannedStart"
                        value={activity.plannedStart}
                        onChange={(e) => handleInputChange(index, e)}
                        className="form-control"
                        disabled={index !== 0}
                      />
                    ) : (
                      activity.plannedStart
                    )}
                  </td>
                  <td data-label="Fim Planejado">
                    {editingIndex === index || editingAll ? (
                      <input
                        type="time"
                        name="plannedEnd"
                        value={activity.plannedEnd}
                        onChange={(e) => handleInputChange(index, e)}
                        onBlur={(e) => handleBlur(index, e)}
                        className="form-control"
                      />
                    ) : (
                      activity.plannedEnd
                    )}
                  </td>
                  <td data-label="Duração Planejada">{activity.plannedDuration}</td>
                  <td data-label="Início Realizado">
                    {editingIndex === index || editingAll ? (
                      <input
                        type="time"
                        name="actualStart"
                        value={activity.actualStart}
                        onChange={(e) => handleInputChange(index, e)}
                        className="form-control"
                        disabled={index !== 0}
                      />
                    ) : (
                      activity.actualStart
                    )}
                  </td>
                  <td data-label="Fim Realizado">
                    {editingIndex === index || editingAll ? (
                      <input
                        type="time"
                        name="actualEnd"
                        value={activity.actualEnd}
                        onChange={(e) => handleInputChange(index, e)}
                        onBlur={(e) => handleBlur(index, e)}
                        className="form-control"
                      />
                    ) : (
                      activity.actualEnd
                    )}
                  </td>
                  <td data-label="Duração Realizada">{activity.actualDuration}</td>
                  <td data-label="Status">{activity.status}</td>
                  <td data-label="Ações" className="text-center">
                    <div className="d-flex justify-content-center">
                      {editingIndex === index || editingAll ? (
                        <>
                          <button type="button" className="btn btn-success btn-sm mx-1" onClick={() => handleSaveActivity(index)}> 💾 Salvar</button>
                          <button type="button" className="btn btn-secondary btn-sm mx-1" onClick={() => handleCancelActivity(index)}> ❌ Cancelar</button>
                        </>
                      ) : (
                        <>
                          <button type="button" className="btn btn-primary btn-sm mx-1" onClick={() => handleEditActivity(index)}>✏️ Editar</button>
                          <button type="button" className="btn btn-danger btn-sm mx-1" onClick={() => handleDeleteActivity(index)}>🗑️ Excluir</button>
                        </>
                      )}
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
    </div>
  );
};

export default ActivityForm;
